            column-gap: 1rem;
}

.myLogo {
    width: 2.5rem;
    height: 2.5rem;
    object-fit: contain;
    text-align: center;
}

.web-name {
    font-family: 'Satisfy', cursive;
    font-weight: 400;
}

.nav-buttons {
    padding: 1rem;
    background-color: inherit;
    border: none;
    outline: none;
    cursor: pointer;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-row-gap: 0.75rem;
    row-gap: 0.75rem;
    font-size: 0.75rem;
    width: 100%;
}

.nav-icons {
    background-color: inherit;
    width: 1.2rem;
    height: 1.2rem;
    object-fit: cover;
    pointer-events: none
}

.nav-name {
    pointer-events: none;
    font-weight: bolder;
    
    background: linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%); 
    -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}


